const main = {
    init() {
        let headerMenu = $('.header__menu');
        let menu = $('.menu');
        let body = $('body');
        let header = $('.header');

        headerMenu.on('click', function () {
            headerMenu.toggleClass('header__menu--active');
            menu.toggleClass('menu--active');
            body.toggleClass('body--overflow');

            if (!header.hasClass('header--active') && $(window).scrollTop() == 0) {
                header.toggleClass('header--active');
            }
        });

        let mySwiper = new Swiper('.slider-second', {
            speed: 400,
            spaceBetween: 100,
            navigation: {
                nextEl: '.swiper-button-next',
                prevEl: '.swiper-button-prev',
            },
            on: {
                slideChange() {
                    firstSlider.html(mySwiper.activeIndex + 1);
                }
            },
        });

        let firstSlider = $('.swiper-pagination__first');
        let allSlider = $('.swiper-pagination__last');

        firstSlider.html(mySwiper.activeIndex + 1);
        allSlider.html(mySwiper.slides.length);

        $( window ).scroll(function() {
            if ($(window).scrollTop() > 0) {
                header.addClass('header--active');
            } else {
                header.removeClass('header--active');
            }
        });
    }
};

export default main;



